.contact-btn {
    height: 6vh;
    width: 12vw;
    margin: 3vh 5vw;
    font-family: helvectica;
    font-size: 1.2vw;
    color: white;
    text-transform: uppercase;
    outline: none;
    background-color: blue;
    cursor: pointer;
    border-radius: 10px;
    transition: background-color 0.2s;

}

.contact-btn:hover {
  color: blue;
  background: white;
}

.btn {
    position: relative;
    border: none;
    height: 6vh;
    width: 7vw;
    outline: none;
    margin: 1vh 2vw;
    background: none;
    /* padding-bottom: 1px; */
    font-family: 'Nunito', sans-serif;
    font-size: 1.1vw;
    color: white;
    border-bottom: 4px solid white;
    text-transform: uppercase;
    overflow:hidden;
    background: none;
    z-index: 1;
    cursor: pointer;
    transition:         0.08s ease-in;
    -o-transition:      0.08s ease-in;
    -ms-transition:     0.08s ease-in;
    -moz-transition:    0.08s ease-in;
    -webkit-transition: 0.08s ease-in;
  }
.fill:hover {
    color: black;
  }
  .fill:before {
    content: "";
    position: absolute;
    background: white;
    bottom: 0;
    left: 0;
    right: 0;
    top: 100%;
    z-index: -1;
    -webkit-transition: top 0.09s ease-in;
  }
  
  .fill:hover:before {
    top: 0;
  }

  @media screen and (max-width: 1024px) {
    .btn {
      font-size: 1.8vw;
      width: 13vw;
      height: 4vh;
      letter-spacing: 0.3vw;
      border-bottom: 0.5vh solid white;
    }

    .contact-btn {
      font-size: 1.8vw;
      width: 17vw;
      height: 4vh;
      letter-spacing: 0.2vw;
      height: 4vh;
      min-height: 40px;
    }

    .display-picture {
      margin: 1.2vh 1.2vw;
    }

    .nav-container {
      height: 10vh;
    }
    .display-picture {
      width: 10vw;
      height: 10vw;
    }
  }

  @media screen and (max-width: 414px) {
    .contact-btn {
      width: -webkit-fit-content;
      width: -moz-fit-content;
      width: fit-content;
      height: 1vw;
    }
  }

